.wp-block-create-block-fragment-amzn {
	@media screen and (min-width: 704px) {
		div.wp-block-create-block-fragment-amzn-inner {
			display: flex;
			max-width: 830px;
			margin: 0;
			padding: 20px;
			background-color: #fff;
			border: double #CCC!important;
			box-shadow: 2px 3px 9px -5px rgb(0 0 0 / 25%);
			div.wp-block-create-block-fragment-amzn-img {
				height: unset;
				margin-right: 20px;
			    position: relative;
				p.wp-block-create-block-fragment-amzn-img-box {
					position: relative;
					width: 160px;
					height: 160px;
					display: flex;
					justify-content: center;
					align-items: center;
					margin: 0;
					padding: 0;
					span {
						display: flex;
						justify-content: center;
						align-items: center;
						position: absolute;
						width: 100%;
						height: 100%;
						text-align: center;
						overflow: hidden;
						z-index: 0;
						vertical-align: middle;
						img {
							position: relative;
							max-width: 160px;
							max-height: 160px;
							margin: 0;
							padding: 0;
							border: 0;
							outline: 0;
							word-break: break-all;
							height: auto;
						}
					}
				}
			}
			div.wp-block-create-block-fragment-amzn-info {
				width: 100%;
				flex-grow: 2;
				margin: 0;
				padding: 0;
				border: 0;
				outline: 0;
				word-break: break-all;
				p.wp-block-create-block-fragment-amzn-info-maker {
					font-size: 12px;
					margin: 0;
					margin-bottom: 5px;
					padding: 0;
					border: 0;
					outline: 0;
					word-break: break-all;
				}
				p.wp-block-create-block-fragment-amzn-info-title {
					font-size: 16px;
					margin-bottom: 5px;
					word-wrap: break-word;
					white-space: pre-wrap;
					a {
						text-decoration: none;
					}
				}
				p.wp-block-create-block-fragment-amzn-info-btn {
					margin-top: 20px;
					display: flex;
					flex-wrap: wrap;
					justify-content: flex-start;
					a {
						text-decoration: none;
						width: 180px;
						margin-right: 10px;
						height: 40px;
						font-size: 12px;
						&.wp-block-create-block-fragment-amzn-info-amazon {
							margin-bottom: 10px;
							padding: 0 5px;
							box-sizing: border-box;
							border-radius: 4px;
							display: flex;
							justify-content: center;
							align-items: center;
							color: #fff;
							background: linear-gradient(45deg, #f7820f, #f79821);
    						box-shadow: 0 3px 7px 0 rgb(0 0 0 / 25%);
							font-weight: bold;
							font-size: 14px;
							line-height: 1.25;
							text-align: center;
							transition: 0.3s ease-in-out;
							text-decoration: none;
						}
						&.wp-block-create-block-fragment-amzn-info-rakuten {
							margin-bottom: 10px;
							padding: 0 5px;
							box-sizing: border-box;
							border-radius: 4px;
							display: flex;
							justify-content: center;
							align-items: center;
							color: #fff;
							background: #cf4944!important;
    						box-shadow: 0 3px 7px 0 rgb(0 0 0 / 25%);
							font-weight: bold;
							font-size: 14px;
							line-height: 1.25;
							text-align: center;
							transition: 0.3s ease-in-out;
							text-decoration: none;
						}
					}
				}
			}
		}
	}
